section {
    padding: 75px 0; }


section h2.section-heading {
    margin-top: 0;
    margin-bottom: 15px;
    font-size: 40px; }


section h3.section-subheading {
    margin-bottom: 75px;
    text-transform: none;
    font-family: "Droid Serif","Helvetica Neue",Helvetica,Arial,sans-serif;
    font-size: 16px;
    font-style: italic;
    font-weight: 400; }


@media(min-width:768px) {
    section {
        padding: 75px 0; } }



.service-heading {
    margin: 15px 0;
    text-transform: none; }


#portfolio .portfolio-item {
    right: 0;
    margin: 0 0 15px; }


#portfolio .portfolio-item .portfolio-link {
    display: block;
    position: relative;
