  .timeline > li .timeline-image { width: 170px; height: 170px; margin-left: -85px; }
  .timeline > li .timeline-image h4 { margin-top: 40px; }
  .timeline > li.timeline-inverted > .timeline-panel { padding: 0 100px 20px 20px; } }

